.lit-spinner,.lit-spinner-black,.lit-spinner-big{
  width:30px;
  height:30px;
  border:2.5px solid rgba(0,0,0,0);
  border-top:2.5px solid #f3f3f3;
  border-bottom:2.5px solid #f3f3f3;
  border-radius:50%;
  -webkit-animation:spin 1s linear infinite;
  animation:spin 1s linear infinite
}

.lit-spinner-black,.lit-spinner-big{
  border-top:2.5px solid #000;
  border-bottom:2.5px solid #000
}

.lit-spinner-big{
  width:6rem !important;
  height:6rem !important
}

@-webkit-keyframes spin{
  from{
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }

  to{
    -webkit-transform:rotate(360deg);
    transform:rotate(360deg)
  }
}

@keyframes spin{
  from{
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }

  to{
    -webkit-transform:rotate(360deg);
    transform:rotate(360deg)
  }
}

.lit-btn{
  outline:none !important;
  background-image:linear-gradient(135deg, #329cff, #0059ff);
  border:none;
  font-size:18px;
  padding:8px 18px;
  border-radius:5px;
  color:#fff;
  box-shadow:4px 4px 5px rgba(20,20,20,.2);
  transition:200ms;
  text-transform:uppercase
}

.lit-btn:hover{
  -webkit-filter:brightness(1.1);
  filter:brightness(1.1);
  box-shadow:4px 4px 5px rgba(20,20,20,.25);
  text-decoration:none;
  color:#fff
}

.blog-btn img{
  -webkit-transform:translateY(0px);
  transform:translateY(0px);
  transition:200ms
}

.blog-btn:hover img{
  -webkit-transform:translateX(2.5px);
  transform:translateX(2.5px);
  transition:200ms
}
/* Make clicks pass-through */

#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;
  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
}

/* Fancy blur effect */

#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;
  -webkit-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */

#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;
  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  -webkit-animation: nprogress-spinner 400ms linear infinite;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg);
  }

  100% { -webkit-transform: rotate(360deg);
  }
}

@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg);
  }

  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);
  }
}


.arrow-wrapper {
  width: 70px;
  display: block;
  margin: auto;
}

@-webkit-keyframes arrow-bounce {
  0%,
	20%,
	50%,
	80%,
	100% {
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  40% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }

  60% {
    -webkit-transform: translateY(15px);
    transform: translateY(15px);
  }
}

@keyframes arrow-bounce {
  0%,
	20%,
	50%,
	80%,
	100% {
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  40% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }

  60% {
    -webkit-transform: translateY(15px);
    transform: translateY(15px);
  }
}

.arrow-wrapper path {
  stroke: #ffffff;
  fill: transparent;
  stroke-width: 1px;
  -webkit-animation: arrow-bounce 2s infinite;
  animation: arrow-bounce 2s infinite;
}

